<template>
  <div class="load">
    <a-spin :spinning="detailFormState.spinning" tip="Loading...">
      <div class="process-box">
        <!-- 标题 -->
        <div class="header-box">
          <BillTitle :options="billTitleOption" />
          <WfApproveBox
            v-if="detailFormState.status != 10"
            @save="onSave"
            @submit="onSubmit"
            :processInstanceId="detailFormState.processInstanceId"
            :processStatus="detailFormState.status"
          />
        </div>
        <!-- 表单内容 -->
        <div class="content">
          <a-form ref="formRef" :model="detailFormState" labelAlign="right" autocomplete="off">
            <a-card title="基本信息" :bordered="false">
              <a-row :gutter="24">
                <a-col v-show="true" :span="12">
                  <a-form-item
                    name="stationName"
                    label="电站名称"
                    :labelCol="{ span: 6 }"
                    :rules="[{ required: true, message: '请输入电站名称' }]"
                  >
                    <a-input
                      disabled
                      v-model:value="detailFormState.stationName"
                      placeholder="请输入电站名称"
                    />
                    <a-input disabled v-model:value="detailFormState.id" v-if="false" />
                  </a-form-item>
                </a-col>
                <a-col v-show="true" :span="12">
                  <a-form-item
                    name="issuedCompany"
                    label="下发单位"
                    :labelCol="{ span: 6 }"
                    :rules="[{ required: true, message: '请输入下发单位' }]"
                  >
                    <a-input
                      v-model:value="detailFormState.issuedCompany"
                      placeholder="请输入下发单位"
                      :disabled="detailFormState.status != 0"
                      :maxLength="100"
                    />
                  </a-form-item>
                </a-col>
              </a-row>
              <a-row :gutter="24">
                <a-col v-show="true" :span="12">
                  <a-form-item
                    name="powerCutType"
                    label="检修类型"
                    :labelCol="{ span: 6 }"
                    :rules="[{ required: true, message: '请选择检修类型' }]"
                  >
                    <DictSelectBox
                      :type="`PowerCutType`"
                      v-model:value="detailFormState.powerCutType"
                      style="width: 100%"
                      :disabled="detailFormState.status != 0"
                    />
                  </a-form-item>
                </a-col>
                <a-col v-show="true" :span="12">
                  <a-form-item
                    name="powerCutKind"
                    label="停电类型"
                    :labelCol="{ span: 6 }"
                    :rules="[{ required: true, message: '请选择停电类型' }]"
                  >
                    <DictSelectBox
                      :type="`PowerCutKind`"
                      @change="`()=> detailFormState.powerCutKind = value`"
                      v-model:value="detailFormState.powerCutKind"
                      style="width: 100%"
                      :disabled="detailFormState.status != 0"
                    />
                  </a-form-item>
                </a-col>
              </a-row>

              <a-row :gutter="24">
                <a-col v-show="true" :span="12">
                  <a-form-item
                    name="startTime"
                    label="停电时间"
                    :labelCol="{ span: 6 }"
                    :rules="[{ required: true, message: '请选择停电时间' }]"
                  >
                    <a-date-picker
                      v-model:value="detailFormState.startTime"
                      value-format="YYYY-MM-DD HH:mm:00"
                      format="YYYY-MM-DD HH:mm"
                      style="min-width: 100%"
                      show-time
                      :disabled="detailFormState.status != 0"
                    />
                  </a-form-item>
                </a-col>
                <a-col v-show="true" :span="12">
                  <a-form-item
                    name="endTime"
                    label="恢复时间"
                    :labelCol="{ span: 6 }"
                    :rules="[{ required: true, message: '请选择恢复时间' }]"
                  >
                    <a-date-picker
                      v-model:value="detailFormState.endTime"
                      value-format="YYYY-MM-DD HH:mm:00"
                      format="YYYY-MM-DD HH:mm"
                      style="min-width: 100%"
                      show-time
                      :disabled="detailFormState.status != 0"
                    />
                  </a-form-item>
                </a-col>
              </a-row>

              <a-row :gutter="24">
                <a-col v-show="true" :span="24">
                  <a-form-item
                    name="name"
                    label="检修停电主题"
                    :labelCol="{ span: 3 }"
                    :rules="[{ required: true, message: '请输入检修停电主题' }]"
                  >
                    <a-input
                      v-model:value="detailFormState.name"
                      placeholder="请输入检修停电主题"
                      :disabled="detailFormState.status != 0"
                      :maxlength="100"
                    />
                  </a-form-item>
                </a-col>
              </a-row>

              <a-row :gutter="24">
                <a-col v-show="true" :span="24">
                  <a-form-item
                    name="content"
                    label="检修停电内容"
                    :labelCol="{ span: 3 }"
                    :rules="[{ required: false, message: '请输入检修停电内容' }]"
                  >
                    <a-textarea
                      v-model:value="detailFormState.content"
                      row="8"
                      show-count
                      style="height: 200px"
                      :disabled="disabledInput"
                      :maxlength="1000"
                    />
                  </a-form-item>
                </a-col>
              </a-row>
            </a-card>
            <a-card title="附件信息" :bordered="false">
              <div class="clearfix" style="padding-left: 15px">
                <UploadBox
                  :width="800"
                  :height="550"
                  :maxCount="20"
                  :maxSize="100 * 1024 * 1024"
                  :application="`po`"
                  :module="`powerCutPlan`"
                  :vmode="detailFormState.status == 0 ? `box` : `view`"
                  :bizId="detailFormState.bizFileId"
                />
              </div>
            </a-card>
          </a-form>
        </div>
      </div>
    </a-spin>
  </div>
</template>

<script lang="ts" setup>
  import { reactive, ref, onMounted, unref } from 'vue';
  import { saveData, initInfoItems, getIndex } from './powerCutPlan';
  import { FormInstance, SelectProps, message } from 'ant-design-vue';
  import { useRouter } from 'vue-router';
  import WfApproveBox from '/@/components/Framework/WorkFlow/WfApproveBox.vue';
  import { assign } from 'lodash-es';
  import * as ProcessInstanceApi from '@/api/bpm/processInstance';
  import BillTitle from '/@/components/Framework/BillTitle/BillTitle.vue';

  import DictSelectBox from '@/components/Framework/Combox/DictSelectBox.vue';
  import UploadBox from '@/components/Framework/Combox/UploadBox.vue';
  import * as FileApi from '@/api/infra/file';
  import { closeCurrentTab } from '@/utils/route';
  import { MsgManager } from '/@/message/MsgManager';
  import { useUserStore } from '/@/store/modules/user';
  import { Memory } from '@/router/index';

  const detailFormState = reactive({ spinning: false });
  const userStore = useUserStore();
  const getUserInfo = userStore.getUserInfo;
  const disabledInput = ref<boolean>(true);
  const billTitleOption = reactive<any>({});
  billTitleOption.subject = '检修停电';
  billTitleOption.infoItems = [];
  const router = useRouter();
  const { currentRoute } = router;
  const route = unref(currentRoute);
  const query = Memory.getInstance().getQuery(); // route.query;

  const formRef = ref<FormInstance>();

  function save(status) {
    detailFormState.spinning = true;
    try {
      status = status || 0;

      formRef.value.validateFields().then(() => {
        detailFormState.status = status;
        saveData(detailFormState).then((data) => {
          detailFormState.id = data;
          getIndex({ id: data }).then((data) => {
            assign(detailFormState, data.result);
          });
          message.success(status == 1 ? '提交成功。' : '保存成功');
          MsgManager.getInstance().sendMsg('powerCutPlan', {});
          if (status == 1) {
            disabledInput.value = true;
            closeCurrentTab();
          }
        });
      });
    } catch (error) {
      console.log('error', error);
    } finally {
      detailFormState.spinning = false;
    }
  }

  function onSubmit() {
    save(1);
  }

  function onSave() {
    save(0);
  }
  assign(detailFormState, {
    processInstanceId: '',
    id: '',
    billCode: '',
    fillinDate: '',
    organId: '',
    organName: '',
    deptId: '',
    deptName: '',
    positionId: '',
    positionName: '',
    personMemberId: '',
    personMemberName: '',
    fullId: '',
    status: 0,
    bizFileId: '',
    subject: '',
    finishedDate: '',
    issuedCompany: '',
    powerCutType: '',
    powerCutKind: '',
    startTime: '',
    endTime: '',
    content: '',
    name: '',
  });
  onMounted(async () => {
    query.id = await getBizParamId();

    detailFormState.status = 0;
    detailFormState.processInstanceId = '';
    await getIndex({ id: query.id }).then((data) => {
      if (query.id) {
        assign(detailFormState, data.result);
        if (
          detailFormState.status == 0 &&
          detailFormState.personMemberId.substring(0, 32) != getUserInfo.userId
        ) {
          detailFormState.status = 10;
        }
      } else {
        assign(detailFormState, query);
        detailFormState.spinning = false;
        detailFormState.status = 0;
        detailFormState.billCode = data.result.billCode;
        detailFormState.personMemberName = data.result.personMemberName;
        detailFormState.fillinDate = data.result.fillinDate;
        detailFormState.deptName = data.result.deptName;
      }

      if (detailFormState.status == 1) {
        disabledInput.value = true;
      }
    });

    if (!query.id) {
      disabledInput.value = false;
    }
    await getRecordable();

    if (
      detailFormState.bizFileId == null ||
      detailFormState.bizFileId == undefined ||
      detailFormState.bizFileId == ''
    ) {
      getBizFileId();
    }
  });

  //==================================附件资料  begin ==================================

  const getBizFileId = async () => {
    FileApi.getBizId().then((res) => {
      detailFormState.bizFileId = res;
    });
  };

  //==================================附件资料  end ==================================

  const getBizParamId = async () => {
    let id = query.id;
    if (!id && query.processInstanceId) {
      const processInstance = await ProcessInstanceApi.getProcessInstance(
        query.processInstanceId as string,
      );
      const businessKey = processInstance.businessKey;
      query.id = businessKey;
      id = businessKey;
    }
    if (detailFormState.id) {
      await getIndex({ id: detailFormState.id }).then((data) => {
        assign(detailFormState, data.result);
      });
    }
    return id;
  };

  const getRecordable = async () => {
    disabledInput.value = detailFormState.status != 0;
    billTitleOption.title = '检修停电';
    detailFormState.processInstanceId = query.processInstanceId;
    let { billCode, fillinDate, personMemberName, deptName } = detailFormState;
    assign(billTitleOption, { billCode, fillinDate, personMemberName, deptName });
  };
</script>
<style lang="less" scoped></style>
